"use client";
import { createContext, useContext, useMemo } from "react";
import { useSocket } from "@/hooks/useSocket";

// üß© T·∫°o context chia s·∫ª socket to√†n app
const SocketContext = createContext({
  io: null,
  connected: false,
});

// üß± Provider b·ªçc to√†n b·ªô ph·∫ßn client c·ªßa app
export function SocketProvider({ children }) {
  // Hook t·∫°o ho·∫∑c t√°i s·ª≠ d·ª•ng socket instance (c√≥ cache)
  const { io, connected } = useSocket();

  // ‚ö° D√πng useMemo ƒë·ªÉ tr√°nh re-render kh√¥ng c·∫ßn thi·∫øt
  const value = useMemo(() => ({ io, connected }), [io, connected]);

  return (
    <SocketContext.Provider value={value}>
      {children}
    </SocketContext.Provider>
  );
}

// üß© Hook ti·ªán d·ª•ng ƒë·ªÉ d√πng socket ·ªü m·ªçi n∆°i
export const useSocketContext = () => {
  const context = useContext(SocketContext);
  if (!context) {
    throw new Error("useSocketContext must be used within a SocketProvider");
  }
  return context;
};
